import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, Switch, Redirect } from "react-router-dom";


import contac from "./views/examples/contact.js"

import Index from "views/Index.js";
import LandingPage from "views/examples/product.js";
import RegisterPage from "views/examples/RegisterPage.js";
import ProfilePage from "views/examples/ProfilePage.js";
import Clients from "views/IndexSections/clients.js";
import NucleoIcons from "views/IndexSections/NucleoIcons";
import Typography from "views/IndexSections/Typography";

import "assets/css/nucleo-icons.css";

import "assets/scss/blk-design-system-react.scss?v=1.1.0";
import "assets/demo/demo.css";

import CreateCategory from "admin/addCategory.js";

import product from "../src/views/examples/product"

import AddProduct from "admin/addProduct.js";
import Navbars from "views/IndexSections/Navbars.js";
import SimpleExpansionPanel from "views/IndexSections/clients.js";



ReactDOM.render(
  <BrowserRouter>
    <Switch>
      <Route path="/components" component={Index} />
      
      <Route
        path="/product-battery"
        render={props => <LandingPage {...props} />}
      />
      <Route path="/products" component={product} />
      <Route path="/addProduct" component={AddProduct} />
      <Route
        path="/contact"
        render={props => <RegisterPage {...props} />}
      />
      <Route
        path="/profile-page"
        render={props => <ProfilePage {...props} />}
      />
       <Route
        path="/products"
        render={props => <Typography {...props} />}
      />
      


      <Route
        path="/clients"
        component={SimpleExpansionPanel}
      />
   <Route
        path="/cli"
        component={NucleoIcons}
      />
<Route
        path="/addCategory"
        component={CreateCategory}
      />
      
      <Redirect from="/" to="/components" />
    </Switch>
  </BrowserRouter>,
  document.getElementById("root")
);


