import React from "react";

import Firebase from "firebase";
import database from "core/database";


class AddCategory extends React.Component {
  constructor(props) {
    super(props);
    

    this.state = {
      categories: []
    };
   
  }

  componentDidMount() {
    this.getUserData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState !== this.state) {
      this.writeUserData();
    }
  }

  writeUserData = () => {
    Firebase.database()
      .ref("/").child("/categories")
      .set(this.state.categories);
    console.log("DATA SAVED");
  };

  getUserData = () => {
    let ref = Firebase.database().ref("/");
    ref.on("value", snapshot => {
      const state = snapshot.val();
      if(state){
        this.setState(state.categories);
      }
    });
  };

  handleSubmit = event => {
    event.preventDefault();
    let name = this.refs.name.value;
    console.log(name)
    
    if (name) {
      const uid = new Date().getTime().toString();
      const { categories} = this.state;
      categories.push({ uid, name });
      this.setState({ categories });
      console.log(categories)
    }

    this.refs.name.value = "";
    
    this.refs.uid.value = "";
  };
  

  removeData = category => {
    const {  categories } = this.state;
    const newState =  categories.filter(data => {
      return data.uid !== category.uid;
    });
    this.setState({ categories: newState });
  };

  updateData = category => {
    this.refs.uid.value = category.uid;
    this.refs.name.value = category.name;
   
    Firebase.database().ref("/").update(category)
}

  render() {
    const { categories } = this.state;
    return (
      <React.Fragment>
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              <h1 className="text-dark">Categories</h1>
            </div>
          </div>
          <div className="row">
            <div className="col-xl-12">
            {categories.map(category => (
                <div
                  key={category.uid}
                  className="card float-left"
                  style={{ width: "18rem", marginRight: "1rem" }}
                >
                  <div className="card-body">
                    <h5 className="card-title">{category.name}</h5>
                    <p className="card-text">{category.role}</p>
                    <button
                      onClick={() => this.removeData(category)}
                      className="btn btn-link"
                    >
                      Delete
                    </button>
                    <button
                      onClick={() => this.updateData(category)}
                      className="btn btn-link"
                    >
                      Edit
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="row">
            <div className="col-xl-12">
              <h1  className="text-dark">Create Category Here</h1>
              <form onSubmit={this.handleSubmit}>
                <div className="form-row">
                  <input type="hidden" ref="uid" />
                  <div className="form-group col-md-12">
                    <label>Name</label>
                    <input
                      type="text"
                      ref="name"
                      className="form-control text-dark"
                      placeholder="Name"
                    />
                  </div>
                 
                </div>
                <button type="submit" className="btn btn-primary">
                  Create
                </button>
              </form>
            </div>
          </div>
         
        </div>
      </React.Fragment>
    );
  }
}

export default AddCategory;
