import React, { useState } from "react";

import Firebase from "firebase";
import database from "core/database";
import storage from "firebase";
import FileUploader from "react-firebase-file-uploader";

let cat=[]
let ur=''
class AddProduct extends React.Component {
  constructor(props) {
    super(props);
    

    this.state = {
      products: [],
      
    };
   
  }
 
  componentDidMount() {
    this.getUserData();
    this.getUserCat();
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevState !== this.state) {
      this.writeUserData();
    }
  }

  handleUploadSuccess = filename => {
    
    Firebase
      .storage()
      .ref("images")
      .child(filename)
      .getDownloadURL()
      .then(url =>ur=url );
      console.log("success")
      alert("Image Upload Success")
  };
  writeUserData = () => {
    Firebase.database()
      .ref("/")
      .set(this.state);
    console.log("DATA SAVED");
  };

  getUserData = () => {
    let ref = Firebase.database().ref("/");
    ref.on("value", snapshot => {
      const state = snapshot.val();
      this.setState(state);
    });
  };

  handleSubmit = event => {
    event.preventDefault();
    let name = this.refs.name.value;
    console.log(name)
    let desc = this.refs.desc.value;
    let cat=this.refs.cat.value
    let url=ur
    let uid = this.refs.uid.value;

    if (name && desc) {
      const uid = new Date().getTime().toString();
      const { products} = this.state;
      products.push({ uid, name, desc,cat,url });
      this.setState({ products });
      console.log(products)
      Firebase.database()
      .ref("/")
      .set(this.state);
    console.log("DATA SAVED");
    }
    

    this.refs.name.value = "";
    this.refs.desc.value = "";
    this.refs.cat.value = "";
    url = "";
    this.refs.uid.value = "";
  };

 

  removeData = product => {
    const {  products } = this.state;
    const newState =  products.filter(data => {
      return data.uid !== product.uid;
    });
    this.setState({ products: newState });
  };

  updateData = product => {
    this.refs.uid.value = product.uid;
    this.refs.name.value = product.name;
    this.refs.desc.value = product.desc;
    this.refs.cat.value = product.cat;
    ur=product.url
    Firebase.database().ref("/").update(product)
}

getUserCat = () => {
  let ref = Firebase.database().ref("/").child("/categories")
  ref.on("value", snapshot => {
    const state = snapshot.val();
  if(state){
    console.log(state)
    cat.push(state)
   let f=[]
   f[0]=cat
   console.log(f)
  }
  
  });
  
};


  render() {
    console.log(cat[0])
    const { products } = this.state;
    return (
      <React.Fragment>
        <div className="container">
          <div className="row">
            <div className="col-xl-12">
              <h1 className="text-dark">Categories</h1>
            </div>
          </div>
          <div className="row">
            <div className="col-xl-12">
              {products.map(product => (
                <div
                  key={product.uid}
                  className="card float-left"
                  style={{ width: "18rem", marginRight: "1rem" }}
                >
                  <div className="card-body">
                    <h5 className="card-title">{product.name}</h5>
                    <h6 className="card-title">{product.cat}</h6>
                    <p className="card-text">{product.desc}</p>
                    <button
                      onClick={() => this.removeData(product)}
                      className="btn btn-link"
                    >
                      Delete
                    </button>
                    <button
                      onClick={() => this.updateData(product)}
                      className="btn btn-link"
                    >
                      Edit
                    </button>
                  </div>
                </div>
              ))}
            </div>
          </div>
          <div className="row">
            <div className="col-xl-12">
              <h1 className="text-dark">Create Product Here</h1>
              <form onSubmit={this.handleSubmit}>
                <div className="form-row">
                  <input type="hidden" ref="uid" />
                  <div className="form-group col-md-6">
                    <label>Name</label>
                    <input
                    
                      type="text"
                      ref="name"
                      className="form-control text-dark"
                      placeholder="Name"
                    />
                  </div>
                  
                  <div className="form-group col-md-6">
                  <label>Category</label>
                  <select
                   ref="cat"
                    className="form-control text-dark"
                    placeholder="Category"
                  >
                    
                    <option>Select</option>
                    { cat.map((data,index)=>{
  
                        console.log(data)
                        for(let i=0;i<cat.length;i++){
                          console.log(data[i])
                          return(
                            <option key={index}>{data[i].name}</option>
                          )
                        }
                      })}
                   
                    
                  </select>

                    </div>
                    <div className="form-group col-md-6">
                    <label>Description</label>
                    <textarea
                      type="text"
                      ref="desc"
                      className="form-control text-dark"
                      placeholder="Product Description"
                    />
                  </div>
                  <div>
                   
                 
                    <FileUploader
                      accept="image/*"
                      name="avatar"
                      randomizeFilename
                      storageRef={Firebase.storage().ref("images")}
                    
                      onUploadSuccess={this.handleUploadSuccess}
                      
                    />
                  </div>
                  
                </div>
                <button type="submit" className="btn btn-primary">
                  Save
                </button>
              </form>
            </div>
          </div>
        
        </div>
      </React.Fragment>
    );
  }
}

export default AddProduct;
