
import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
//import Tabs from '@material-ui/core/Tabs';
//import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import Firebase from "firebase";
import { darkBlack } from 'material-ui/styles/colors';
import Tabs from 'react-bootstrap/Tabs'
import Tab from 'react-bootstrap/Tab'
import { Container } from '@material-ui/core';
import { MDBBtn, MDBCard, MDBCardBody, MDBCardImage, MDBCardTitle, MDBCardText, MDBRow, MDBCol, MDBIcon } from
'mdbreact';
import IndexNavbar from "components/Navbars/IndexNavbar.js";
import Footer from 'components/Footer/Footer';

let c=1;
let state,dat,a=[]
let cat=[]

class product extends React.Component{
  
  constructor(props){
    super(props)

    this.state ={
      products:[],
      key:'home'
    }
  }
  componentDidMount() {
    this.getUserData();
  }




  getUserData = () => {
    let ref = Firebase.database().ref("/").child("/products")
    ref.on("value", snapshot => {
      const state = snapshot.val();
      console.log(state)
       this.setState({
         products:state
       })
     
      
    });
  };
  
  render(){
    
    const { products } = this.state;
    console.log(products)
    return (
    
    <div>
      <IndexNavbar />
    <Container>
      <h1 style={{marginTop:"15vh"}} className="text-dark">Products</h1>
        <div >
        
        {products.map((product)=>{
          console.log(product)
        return(
          
          <MDBRow >
          <MDBCol style={{ maxWidth: "40rem" }}>
            <MDBCard  reverse>
              <MDBCardImage cascade style={{ height: '20rem' }} src={product.url} />
              <MDBCardBody style={{backgroundColor:"#4c8bf5"}} cascade className="text-center">
        <MDBCardTitle>{product.name}</MDBCardTitle>
                <h5 className="indigo-text"><strong>{product.cat}</strong></h5>
        <MDBCardText>{product.desc}</MDBCardText>
                <a href="#!" className="icons-sm li-ic ml-1">
                  <MDBIcon fab icon="linkedin-in" /></a>
                <a href="#!" className="icons-sm tw-ic ml-1">
                  <MDBIcon fab icon="twitter" /></a>
                <a href="#!" className="icons-sm fb-ic ml-1">
                  <MDBIcon fab icon="facebook-f" /></a>
              </MDBCardBody>
            </MDBCard>
          </MDBCol>
        </MDBRow>
        )
      })}
     
      
      </div>
    </Container>
    
      <Footer/>
    </div>
    );
   
  }
 
}
export default product;