database()
class Index extends React.Component {
  constructor(props) {
    super(props);
    
    
    this.state = {
      categories: []
    };
  }
  componentDidMount() {
    document.body.classList.toggle("index-page");
    this.getUserData();
  }
  componentWillUnmount() {
    document.body.classList.toggle("index-page");
  }
 
 

  getUserData = () => {
    let ref = Firebase.database().ref("/");
    ref.on("value", snapshot => {
      const state = snapshot.val();
      this.setState(state);
    });
  };



  removeData = category => {
    const {  categories } = this.state;
    const newState =  categories.filter(data => {
      return data.uid !== category.uid;
    });
    this.setState({ categories: newState });
  };

  updateData = category => {
    this.refs.uid.value = category.uid;
    this.refs.name.value = category.name;
    this.refs.role.value = category.role;
    Firebase.database().ref("/").update(category)
}
  render() {
    const { categories } = this.state;
    return (
      <>
           
        <IndexNavbar/>
        <div className="wrapper">
          <PageHeader />
          
          <div className="main">
       
        
          <img className="client" style={{marginBottom:"-2vh",marginLeft:"2vh"}}
              alt="..."
              
              src={require("../assets/img/Group 359.png")}
            />
         
         
            
           { /*
            <Navbars />
            <Basics />
            <Tabs />
           
            <Pagination />
            <Notifications />
            
            <JavaScript />
            <Examples />
            <Download />
            */
           }
            
            
            <Signup />
            <section className="section section-lg">
            <section className="section">
              <img
                alt="..."
                className="path"
                src={require("assets/img/path4.png")}
              />
           
            </section>
          </section>
          <React.Fragment>
        <Container>
        <div className="row">
            <div className="col-xl-12">
              {categories.map(category => (
                <div
                  key={category.uid}
                  className="card float-left"
                  style={{ width: "18rem", marginRight: "1rem" }}
                >
                  <div className="card-body">
                    <h3 className="card-title">{category.name}</h3>
                    <p className="card-text">{category.role}</p>
                   
                  </div>
                </div>
              ))}
            </div>
          </div>
         
          </Container>
        
      </React.Fragment>
          </div>
         
          <Footer />
          
        </div>
      </>
    );
  }
}

export default Index;
